<template>
  <div class="bank-card-page">
    <div v-if="isBindYHK">
      <!-- <div class="card-item-preview" :style="{'background-color': getBankStyleInfo[bankInfo.bankNo].background}">
        <div class="card-preview-left">
          <div><img :src="'//cdn09.ehaier.com/shunguang/H5/www/img/'+bankInfo.bankEcode+'.png'" alt=""></div>
        </div>
        <div class="card-preview-right">
          <div>{{bankInfo.bankName}} <img src="//cdn09.ehaier.com/shunguang/H5/www/img/baozhang.png" :alt="bankInfo.bankName"></div>
          <p>{{bankInfo.cardNo}}</p>
        </div>
        <img class="card-preview-bg" :src="'//cdn09.ehaier.com/shunguang/H5/www/img/'+bankInfo.bankEcode+'.png'" alt="">
      </div> -->
      <van-cell-group>
          <van-field
            v-model="phone"
            label="手机号"
            disabled
          />
          <van-field
            v-model="sms"
            center
            clearable
            label="短信验证码"
            placeholder="请输入短信验证码"
            maxlength="6"
          >
            <van-button slot="button" size="small" type="primary" :disabled="disabled" @click="getCode">{{btnText}}</van-button>
          </van-field>
        </van-cell-group>
        <div class="do-unbind">
          <a href="javascript:;" @click="doUnbind">解除绑定</a>
        </div>
    </div>
    <div v-else class="bank-card-nobind">
      您还未绑定银行卡
    </div>
  </div>
</template>

<script>
import URL from '@/http/url'
import { mapGetters } from 'vuex'

export default {
  name: 'unbindcard',
  data() {
    return {
      isBindYHK: false,
      bankInfo: {},
      phone: '',
      sms: '',
      errMessage: '',
      cardNo: '',
      btnText: '获取验证码',
      disabled: false,
    }
  },
  mounted() {
    this.cardNo = this.$route.params.cardNo
    this.fetchInit()
  },
  computed: {
    ...mapGetters('bankcard', ['getBankStyleInfo']),
  },
  methods: {
    fetchInit() {
      this.$http.get(URL.GET_BANK_DETAIL_INFO + '?cardNo=' + this.cardNo).then(({ data }) => {
        let result = data.data;
        this.isBindYHK = result.isBind
        this.bankInfo = result || {}
        this.phone = result.phoneNo
      })
    },
    getCode() {
      this.$http.get(URL.BANK_DELETE_CAPTCHA + '?cardNo=' + this.cardNo).then(res => {
        let result = res.data;
        if (result.success) {
          this.$toast('您正在操作解绑银行卡，短信验证码已经发送到您绑定银行卡时的预留银行手机号' + result.data + '中，请查收并进行验证。')
          this.countdown()
        }
      })
    },
    doUnbind() {
      if (!this.sms) {
        this.$toast('请输入手机收到的验证码')
        return
      }
      this.$dialog.confirm({
        title: '确定要解绑该银行卡?'
      }).then(() => {
        this.$http.get(URL.BANK_DELETE_NEW + '?cardNo=' + this.cardNo + '&captcha=' + this.sms).then(res => {
          if (res.data.success) {
            this.$toast('您的银行卡已经解绑成功！')
            setTimeout(() => {
              this.$router.back()
            }, 2000)
          } else {
            this.$dialog.confirm({
              title: res.data.message
            })
          }
        })
      }).catch(() => {

      });
    },
    countdown() {
      let count = 60;
      let timer = null;
      let run = () => {
        if (count <= 0) {
          clearInterval(timer);
          this.btnText = '重新获取'
          this.disabled = false
          return;
        }
        count--;
        this.btnText = count + 's'
      }
      timer = setInterval(run, 1000)
      this.disabled = true
    },
  }
}
</script>

